import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { MenuController } from '@ionic/angular';
import { Router } from '@angular/router';
import { ToastService } from 'src/app/toast.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { FacturaspagadasService } from 'src/app/facturaspagadas/facturaspagadas.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  form: FormGroup;
  rut;
  modelRut;
  submit = false;
  constructor(
    private authService: AuthService,
    public menuCtrl: MenuController,
    private router: Router,
    private toast: ToastService,
    private dataService: AuthService,
    private fb: FormBuilder,
    private loadingService: FacturaspagadasService
  ) { }

  ionViewWillEnter() {
    this.menuCtrl.enable(false);
  }

  ionViewWillLeave() {
    this.menuCtrl.enable(true);
  }

  ngOnInit() {
    this.form = this.fb.group({
      rut: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  login() {
    this.loadingService.presentLoading('Validando Datos...');
    this.submit = true;
    if (this.form.value.rut && this.form.value.password) {
      const rut = this.form.value.rut.split('.').join('');
      const _rut = rut.split('-');
      const rutFinal = Number(_rut['0']) + '-' + _rut['1'];
      const password = this.form.value.password;
      // console.log(rut);
      this.dataService.login(rutFinal, password).subscribe((res: any) => {
        if (res.token && res.usuario) {
          localStorage.setItem('token', res.token);
          localStorage.setItem('usuario', res.usuario['0'].nombre);
          this.form.reset();
          this.submit = false;
          this.loadingService.dismissLoading();
          this.router.navigate(['/home']);
        } else {
          this.loadingService.dismissLoading();
          this.submit = false;
          this.toast.presentToast('Error al iniciar Sesion. Usuario no encontrado.');
        }
      }, error => {
        this.loadingService.dismissLoading();
        this.toast.presentToast(error.error.error);
        this.submit = false;
      });
    } else {
      this.loadingService.dismissLoading();
      this.toast.presentToast('Error al iniciar Sesion. Debe ingresar un rut y contraseña.');
      this.submit = false;
    }
  }

  logout() {
    this.authService.logout();
  }

}
