import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoadingController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class FacturaspagadasService {
  // apiUrl = 'http://127.0.0.1:8000/api/';
  private apiUrl = 'https://coagra.api.smartportal.cl/';
  constructor(private httpClient: HttpClient, private loadingController: LoadingController) { }

  public getFacturas(moneda) {
    // 88850400-1
    // const rut = localStorage.getItem('rut');
    const ruta = 'clientes/facturas/pendientes?&moneda=' + moneda;
    const token = '&token=' + localStorage.getItem('token');
    return this.httpClient.get(this.apiUrl + ruta + token);
  }

  public getPeriodos() {
    const ruta = 'clientes/cartolas/periodos?';
    const token = 'token=' + localStorage.getItem('token');
    return this.httpClient.get(this.apiUrl + ruta + token);
  }

  public getCartola() {
    const token = localStorage.getItem('token');
    const ruta = 'clientes/cartolas/actual';
    return this.httpClient.get(this.apiUrl + ruta + '?&token=' + token, {
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + localStorage.getItem('token'),
        observe: 'response',
      }),
      responseType: 'blob'
    });
  }

  public getCartolaHistorica(periodo) {
    const token = localStorage.getItem('token');
    const ruta = 'clientes/cartolas/historica';
    return this.httpClient.get(this.apiUrl + ruta + '?&periodo=' + periodo + '&token=' + token, {
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + localStorage.getItem('token'),
        observe: 'response',
      }),
      responseType: 'blob'
    });

  }


  async presentLoading(text) {
    const loading = await this.loadingController.create({
      message: text,
      duration: 0
    });
    await loading.present();
  }

  async dismissLoading() {
    await this.loadingController.dismiss();
  }

}
